@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.container {
  max-width:100%;
  width: 100vw;
  height: 100vh;
  padding: 1rem;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.header-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.title-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.title {
  margin: 0 2rem;
}

.underline {
  border-width: 0 0 .3rem 0;
  border-style: solid;
  width: 12rem;
}

@media (max-width: 480px) {
  .title {
    font-size: 1.5rem;
    margin: 0 1rem;
  }

  .underline {
    border-width: 0 0 .15rem 0;
  }
}

@media (max-width: 320px) {
  .container {
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }
  
  .title {
    font-size: .8rem;
    margin: 0 .5rem;
  }

  .underline {
    border-width: 0 0 .15rem 0;
    width: 5rem;
  }
}
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #ffffff !important;
  -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}
